import styled from 'styled-components';

const StyledButton = styled.button`
	height: 50px;
	width: 100px;
	outline: none;
	border: none;
	background-color: #1b1b1b;
	color: white;
	padding: 10px;
	font-family: Dank Mono;
	font-size: 15px;
	border-radius: 15px;
`;

export default StyledButton;